import React, { Component } from "react";
import Icons from "./Icons";

import {
  faInstagram,
  faLinkedin,
  faTwitter,
  faGithub,
} from "@fortawesome/free-brands-svg-icons";

const Footer = () => {
  return (
    <div>
      <div className="container">
        <h3>Jeremias Amador</h3>
        <p style={{ fontSize: "15px", margin: "5px 0" }}>
          Designed and Developed by Jeremias Amador
        </p>
        <div className="icons">
          <Icons
            type={faLinkedin}
            className="icons__icon"
            url="https://linkedin.com"
          />
          <Icons
            type={faGithub}
            className="icons__icon"
            url="https://github.com/Jerucho/"
          />
        </div>
      </div>
    </div>
  );
};

export default Footer;
